.wp-block-b-blocks-button-group {
	.bBlocksButtonGroup {
		display: inline-flex;
		align-items: stretch;
		max-width: 100%;

		* {
			box-sizing: border-box;
		}

		.bBlocksBtn {
			max-width: 100%;

			a {
				display: inline-flex;
				align-items: center;
				justify-content: center;
				width: 100%;
				max-width: 100%;
				height: 100%;
				text-decoration: none;
				outline: 0;
				box-shadow: none;
				transition: all .3s ease;

				&:hover,
				&:focus,
				&:active {
					text-decoration: none;
					outline: 0;
					box-shadow: none;
				}
			}
		}

		.firstBtn {
			position: relative;
			display: inline-block;

			.btnSeparator {
				position: absolute;
				top: 50%;
				right: 0;
				transform: translate(50%, -50%);
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				overflow: hidden;
				padding: 5px;

				span {
					text-transform: uppercase;
				}

				i {
					font-style: normal;
				}
			}
		}
	}
}